import React from 'react';
import './App.css';
import bank from './bank.json';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedFloor: 0,
      callbacks: {
        onFloorChange: this.handleFloorChange.bind(this),
        onWallToggle: this.handleToggleWall.bind(this),
        onHatchToggle: this.handleToggleHatch.bind(this)
      },
      userData: {
        activeHatches: new Set([]),
        activeWalls: new Set([])
      }
    };
  }
  handleFloorChange(floor) {
    this.setState({selectedFloor: floor});
  }
  handleToggleHatch(key) {
    const newSet = new Set(this.state.userData.activeHatches);
    if (newSet.has(key)) {
       newSet.delete(key);
    } else {
      newSet.add(key);
    }
    const newData = Object.assign({}, this.state.userData);
    newData.activeHatches = newSet;
    this.setState({userData: newData});
  }
  handleToggleWall(key) {
    const newSet = new Set(this.state.userData.activeWalls);
    if (newSet.has(key)) {
       newSet.delete(key);
    } else {
      newSet.add(key);
    }
    const newData = Object.assign({}, this.state.userData);
    newData.activeWalls = newSet;
    this.setState({userData: newData});
  }
  render() {
    return (
      <div className="App">
        <NavBar userData={this.state.userData} selectedFloor={this.state.selectedFloor} callbacks={this.state.callbacks}/>
        <Map mapData={bank} userData={this.state.userData} floor={this.state.selectedFloor} callbacks={this.state.callbacks}/>
      </div>
    );
  }
}
class NavBar extends React.Component {
  render() {
    return (
    <nav>
      <FloorPicker selectedFloor={this.props.selectedFloor} onFloorChange={this.props.callbacks.onFloorChange} />
      <ToolPicker userData={this.props.userData}/>
    </nav>
    )
  }
}

class ToolPicker extends React.Component {
  render() {
    const nReinforces = this.props.userData.activeWalls.size+this.props.userData.activeHatches.size;
    return (
      <div id="tool-selector">
        <button id="reinforce-tool">{nReinforces}</button>
      </div>
    )
  }
}

