class FloorPicker extends React.Component {
  render() {
    const buttons = ["B","1","2","3"].map((floorName, floor) => 
      (
        <FloorButton
          key={floor}
          active={this.props.selectedFloor===floor}
          floor={floor}
          floorName={floorName}
          onClick={this.props.onFloorChange}
        />
      )
    );
    return (
      <div id="floor-selector">
        {buttons}
      </div>
    )
  }
}

class FloorButton extends React.Component {
  render() {
    return (
    <button
      className={this.props.active ? "active" : ""}
      onClick={() => this.props.onClick(this.props.floor)}>
        {this.props.floorName}
    </button>
    )
  }
}

class Map extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mapData: props.mapData
    };
  }
  render() {
    const bombs = this.props.mapData.bombs.map((item, i) =>
      <g key={item.site} className={"bomb"+(i+1)}>
        <Bomb text="a" topLeft={item.a} size={{x:30, y:40}} />
        <Bomb text="b" topLeft={item.b} size={{x:30, y:40}} />
      </g>
    );
    const walls = this.props.mapData.walls.map((item, i) =>
      <Wall key={i} id={i} topLeft={{x: item.x, y: item.y}} size={{x: item.w, y:item.h}} onChange={this.props.callbacks.onWallToggle}
      active={this.props.userData.activeWalls.has(i)}/>
    );
    const hatches = this.props.mapData.hatches.map((item, i) =>
      <Hatch key={i} id={i} topLeft={{x: item.x, y: item.y}} size={{x: item.w, y:item.h}} onChange={this.props.callbacks.onHatchToggle}
      active={this.props.userData.activeHatches.has(i)}/>
    );
    const viewBoxes = ["-1000 -1000 1000 1000", "0 -1000 1000 1000", "-1000 0 1000 1000", "0 0 1000 1000"];
    return (
      <div id="map">
      <svg id="svg" viewBox={viewBoxes[this.props.floor]}>
        <pattern id="diagonal-stripe-4" patternUnits="userSpaceOnUse" width="10" height="10"> <image xlinkHref="data:image/svg+xml;base64,PHN2ZyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnIHdpZHRoPScxMCcgaGVpZ2h0PScxMCc+CiAgPHJlY3Qgd2lkdGg9JzEwJyBoZWlnaHQ9JzEwJyBmaWxsPSdibGFjaycvPgogIDxwYXRoIGQ9J00tMSwxIGwyLC0yCiAgICAgICAgICAgTTAsMTAgbDEwLC0xMAogICAgICAgICAgIE05LDExIGwyLC0yJyBzdHJva2U9J3doaXRlJyBzdHJva2Utd2lkdGg9JzMnLz4KPC9zdmc+" x="0" y="0" width="10" height="10"> </image> </pattern>
        <pattern id="diagonalHatch" patternUnits="userSpaceOnUse" width="4" height="4">
          <path d="M-1,1 l2,-2
                  M0,4 l4,-4
                  M3,5 l2,-2" 
                style={{stroke: "black", strokeWidth:2}} />
        </pattern>
        <mask id="striped-mask" x="0" y="0" width="1" height="1" >
          <rect x="-1000" y="-1000" width="2000" height="2000" fill="url(#diagonal-stripe-4)" />
        </mask>
        <image href="res/bank.png" preserveAspectRatio="none" x="-1000" y="-1000" width="2000" height="2000" />
        {bombs}
        {walls}
        {hatches}
      </svg>
      </div>
    )
  }
}

class Wall extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
    };
  }
  toggleActive() {
    this.setState({active: (!this.state.active)});
  }
  render() {
    return (
      <rect
        className={"wall" + (this.props.active ? " active" : "")}
        x={this.props.topLeft.x}
        y={this.props.topLeft.y}
        width={this.props.size.x}
        height={this.props.size.y}
        onClick={() => this.props.onChange(this.props.id)}
      ></rect>
    )
  }
}

function below(topLeft) {
  if (topLeft.x < 0 && topLeft.y < 0) {
    return topLeft;
  }
  if (topLeft.x >= 0 && topLeft.y < 0) {
    return Object.assign({}, topLeft, {x: topLeft.x-1000});
  }
  if (topLeft.x < 0 && topLeft.y >= 0) {
    return {x: topLeft.x+1000, y: topLeft.y-1000};
  }
  if (topLeft.x >= 0 && topLeft.y >= 0) {
    return Object.assign({}, topLeft, {y: topLeft.y-1000});
  }
}

class Hatch extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
    };
  }
  toggleActive() {
    this.setState({active: (!this.state.active)});
  }
  render() {
    const underTL = below(this.props.topLeft);
    return (
      <g onClick={() => this.props.onChange(this.props.id)}>
      <rect
        className={"overhatch" + (this.props.active ? " active" : "")}
        x={this.props.topLeft.x}
        y={this.props.topLeft.y}
        width={this.props.size.x}
        height={this.props.size.y}
      ></rect>
      <rect
        className={"underhatch" + (this.props.active ? " active" : "")}
        x={underTL.x}
        y={underTL.y}
        width={this.props.size.x}
        height={this.props.size.y}
        mask="url(#striped-mask)">
      </rect></g>
    )
  }
}

class Bomb extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      topLeft: props.topLeft,
      size: props.size,
      text: props.text
    };
  }
  render() {
    return (
      <g>
        <rect
          x={this.state.topLeft.x}
          y={this.state.topLeft.y}
          width={this.state.size.x}
          height={this.state.size.y}
          rx="5"
          ry="5"
          ></rect>
        <text
          x={this.state.topLeft.x+10}
          y={this.state.topLeft.y+25}
          className="bombText"
          >{this.state.text}
          </text>
      </g>
    )
  }
}

export default App;
